  font-weight: bold;
}

.pjs-ui-element {
  color: black;
  background-color: #f8f8f8;
  display: inline-block;
  font-size: 13px;
  border: solid 1px #cdcdcd;
}

button:hover {
  cursor: pointer;
}

input,
textarea {
  width: 100%;
  text-align: left;
  padding: 6px 10px;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.pjs-labeled-input {
  position: relative;
}

.pjs-section-spacer {
  width: 100%;
  height: 1px;
  background-color: #555555;
}

.pjs-io-dialog {
  position: fixed;
  width: 600px;
  left: 50%;
  margin-left: -300px;
  top: 100px;
  background: white;
  padding: 10px 30px 20px 30px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  height: auto;
  z-index: 1001;
}

.pjs-io-dialog.pjs-io-dialog-open::before {
  content: "";
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background: #666;
  opacity: 0.2;
  z-index: 1001;
}

.pjs-io-dialog .pjs-io-dialog-content {
  background: white;
  z-index: 1001;
  position: fixed;
  width: 600px;
  left: 50%;
  margin-left: -300px;
  top: 100px;
  background: white;
  padding: 10px 30px 20px 30px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  height: auto;
}

.pjs-io-dialog-section {
  padding-top: 10px;
}

.pjs-io-dialog-section.pjs-first {
  padding-top: 0px;
}

.pjs-io-dialog-local {
  padding: 10px;
  background: white;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  height: auto;
}

.pjs-io-dialog-local .pjs-box-row {
  min-width: 40px;
}

.pjs-io-dialog-local .pjs-box-row img {
  width: 100%;
  max-width: 100px;
  object-fit: fill;
  vertical-align: middle;
}

.pjs-buttons.pjs-image-selection-submit-wrapper {
  position: relative;
}

.pjs-buttons.pjs-image-selection-submit-wrapper button {
  width: 100%;
}

.pjs-buttons.pjs-image-selection-submit-wrapper button:hover {
  color: #555;
}

.pjs-buttons.pjs-image-selection-submit-wrapper button:active {
  opacity: 1;
  box-shadow: 0px 2px 2px 0 rgba(0, 0, 0, 0.1);
}
